exports.push([module.id, "\n@keyframes jump {\n0%   {transform: translate3d(0,0,0) scale3d(1,1,1);\n}\n40%  {transform: translate3d(0,30%,0) scale3d(.7,1.5,1);\n}\n100% {transform: translate3d(0,100%,0) scale3d(1.5,.7,1);\n}\n}\n.jump.sendingImg, .jump.receivingImg {\n    fill: #000!important;\n    -ms-transform-origin: 50% 50%;\n        transform-origin: 50% 50%;\n    animation: jump .5s linear alternate infinite;\n}\n#myWalletImport{\n    display: none;\n}\n.vue-slider-component.vue-slider-horizontal .vue-slider-dot{\n    left:-5px;\n}\n#walletButtonText{\n    color: #1f1f1f!important;\n}\n#walletButtonText svg{\n    vertical-align: top;\n    width: 14px;\n    margin-left: 10px;\n    margin-top: 5px;\n}\n.mainAddress{\n    background-color: #fec02c14;\n}\n#walletButton {\n    margin: 0 auto;\n    position: fixed;\n    z-index: 85;\n    bottom: 0;\n    width: 299px!important;\n    right: 0;\n    text-align: center;\n    height: 50px;\n    border-top-left-radius: 60px;\n    cursor: pointer;\n    background-color: #fec02c;\n    color: #1f1f1f;\n    margin-bottom: 20px;\n    transition: all .3s linear;\n}\n#walletButton:hover{\n    background-color: #fec02c;\n    transition: all .3s linear;\n}\n.walletSection{\n    display: inline-block;\n    vertical-align: top;\n    overflow-y: auto;\n    overflow-x: hidden;\n    width: 100%;\n}\n.walletController{\n    display: -ms-grid;\n    display: grid;\n    -ms-grid-columns: 1fr 1fr 1fr;\n        grid-template-columns: 1fr 1fr 1fr;\n    position: relative;\n    width: 100%;\n    border-bottom: solid 1px #333333;\n    background-color: #3e3e3e;\n}\n.walletController .btn{\n    text-align: center;\n    color: #b5b5b5;\n    cursor: pointer;\n    padding: 8px 19px 6px 19px!important;\n}\n.walletController .btn:hover{\n    background-color: #575757;\n    transition: all .3s linear;\n}\n.walletController .btn:first-child{\n    border-right: solid 1px #3c3b3b;\n}\n.allWallets div{\n    border: solid 1px #545454;\n}\n#walletButton:hover{\n    transition: all .3s linear;\n}\n#walletButton span{\n    width: 100%;\n    line-height: 50px;\n    font-size: 20px;\n    font-weight: bolder;\n    transition: all .3s linear;\n}\n#walletButton span:hover{\n    transition: all .3s linear;\n}\n.statusWalletIcon{\n    margin-top: 10px!important;\n}\n#walletMenu{\n    margin: 0 auto;\n    position: fixed;\n    bottom: 0;\n    right: 0;\n    width: 300px;\n    background-color: #1f1f1f;\n    height: 358px;\n    margin-bottom:-100px;\n    z-index: 100;\n    border-top: solid 1px #3d3d3d;\n    border-left: solid 1px #393939;\n    transition: all .3s linear;\n}\n.buttonIcon{\n    display: inline-block;\n    margin-right: 10px;\n}\n.editError{\n    color: #ff0000 !important;\n    padding: 1px 0 10px 11px;\n    display: block;\n    font-size: 14px;\n    text-align: left;\n    width: 100%;\n}\n.editError2{\n    padding: 10px 0 10px 11px;\n}\ndashboardWallet span{\n    width: 100%!important;\n    display: block;\n    margin: 0;\n    letter-spacing: 0!important;\n    color: #808080;\n}\n#walletButton .buttonIcon{\n    fill: #000;\n    transition: all .3s linear;\n}\n.walletAddress b{\n    font-weight:100;\n}\n.miningStatus{\n    width: 20px;\n    height: 20px;\n    position: fixed;\n    display: block;\n    right: 4px;\n    bottom: 57px;\n    z-index: 1000;\n    fill:#262626;\n    transition: all 1.2s linear;\n}\n.buttonTextStyle{\n    font-size: 14px!important;\n}\n.sendingImg{\n    fill:#298bea!important;\n}\n.receivingImg{\n    fill:#219411!important;\n}\nlabel.myLabel input[type=\"file\"] {\n    position: fixed;\n    top: -1000px;\n}\n\n/* Small Devices, Tablets */\n@media only screen and (max-width : 831px) {\n.miningStatus{\n        display: none;\n        bottom: 67px;\n}\n#walletMenu{\n        width: 100%;\n        margin-top: 50px!important;\n}\n#walletButton{\n        width: 100%!important;\n        border:0;\n        height: 50px;\n        border-top-left-radius: 15px;\n        border-top-right-radius: 15px;\n        margin-bottom: 90px;\n}\n#walletButton span{\n        line-height: 50px;\n        font-size: 22px;\n}\n.walletController .btn{\n        padding: 10px 19px 6px 19px!important;\n        margin-left: 10px;\n}\n.webdollarFont{\n        width: 20px!important;\n}\n#allWallets .walletAddress{\n        margin: 15px 0 0 10px!important;\n}\n#allWallets .walletAddress img{\n        margin-top: 5px;\n}\n#allWallets .walletAddress .imageAndInput img{\n        margin-top: 0!important;\n}\n.walletAddress b{\n        font-size: 22px!important;\n        line-height: 60px!important;\n}\n#allWallets .walletAddress b{\n        line-height: 0!important;\n}\n.walletController{\n        position: relative;\n        width: 100%;\n        border-bottom: solid 5px #333333;\n        background-color: #313131;\n        border-top: solid 5px #313131;\n}\n#walletButtonText svg{\n        width: 22px;\n}\n}\n\n", "", {"version":3,"sources":["/Users/laurentenhoor/webd/User-Interface-WebDollar/src/components/Wallet/src/components/Wallet/Wallet.vue"],"names":[],"mappings":";AA0ZA;AACA,MAAA,6CAAA;CAAA;AACA,MAAA,kDAAA;CAAA;AACA,MAAA,mDAAA;CAAA;CACA;AACA;IACA,qBAAA;IACA,8BAAA;QAAA,0BAAA;IACA,8CAAA;CACA;AAEA;IACA,cAAA;CACA;AAEA;IACA,UAAA;CACA;AAEA;IACA,yBAAA;CACA;AAEA;IACA,oBAAA;IACA,YAAA;IACA,kBAAA;IACA,gBAAA;CACA;AAEA;IACA,4BAAA;CACA;AAEA;IACA,eAAA;IACA,gBAAA;IACA,YAAA;IACA,UAAA;IACA,uBAAA;IACA,SAAA;IACA,mBAAA;IACA,aAAA;IACA,6BAAA;IACA,gBAAA;IACA,0BAAA;IACA,eAAA;IACA,oBAAA;IACA,2BAAA;CACA;AAEA;IACA,0BAAA;IACA,2BAAA;CACA;AAEA;IACA,sBAAA;IACA,oBAAA;IACA,iBAAA;IACA,mBAAA;IACA,YAAA;CACA;AAEA;IACA,kBAAA;IAAA,cAAA;IACA,8BAAA;QAAA,mCAAA;IACA,mBAAA;IACA,YAAA;IACA,iCAAA;IACA,0BAAA;CACA;AAEA;IACA,mBAAA;IACA,eAAA;IACA,gBAAA;IACA,qCAAA;CACA;AAEA;IACA,0BAAA;IACA,2BAAA;CACA;AAEA;IACA,gCAAA;CACA;AAEA;IACA,0BAAA;CACA;AAEA;IACA,2BAAA;CACA;AAEA;IACA,YAAA;IACA,kBAAA;IACA,gBAAA;IACA,oBAAA;IACA,2BAAA;CACA;AAEA;IACA,2BAAA;CACA;AAEA;IACA,2BAAA;CACA;AAEA;IACA,eAAA;IACA,gBAAA;IACA,UAAA;IACA,SAAA;IACA,aAAA;IACA,0BAAA;IACA,cAAA;IACA,qBAAA;IACA,aAAA;IACA,8BAAA;IACA,+BAAA;IACA,2BAAA;CACA;AAEA;IACA,sBAAA;IACA,mBAAA;CACA;AAEA;IACA,0BAAA;IACA,yBAAA;IACA,eAAA;IACA,gBAAA;IACA,iBAAA;IACA,YAAA;CACA;AAEA;IACA,0BAAA;CACA;AAEA;IACA,sBAAA;IACA,eAAA;IACA,UAAA;IACA,4BAAA;IACA,eAAA;CACA;AAEA;IACA,WAAA;IACA,2BAAA;CACA;AAEA;IACA,gBAAA;CACA;AAEA;IACA,YAAA;IACA,aAAA;IACA,gBAAA;IACA,eAAA;IACA,WAAA;IACA,aAAA;IACA,cAAA;IACA,aAAA;IACA,4BAAA;CACA;AAEA;IACA,0BAAA;CACA;AAEA;IACA,uBAAA;CACA;AAEA;IACA,uBAAA;CACA;AAEA;IACA,gBAAA;IACA,aAAA;CACA;;AAEA,4BAAA;AACA;AAEA;QACA,cAAA;QACA,aAAA;CACA;AAEA;QACA,YAAA;QACA,2BAAA;CACA;AACA;QACA,sBAAA;QACA,SAAA;QACA,aAAA;QACA,6BAAA;QACA,8BAAA;QACA,oBAAA;CACA;AACA;QACA,kBAAA;QACA,gBAAA;CACA;AACA;QACA,sCAAA;QACA,kBAAA;CACA;AACA;QACA,sBAAA;CACA;AACA;QACA,gCAAA;CACA;AACA;QACA,gBAAA;CACA;AACA;QACA,wBAAA;CACA;AACA;QACA,0BAAA;QACA,4BAAA;CACA;AACA;QACA,yBAAA;CACA;AACA;QACA,mBAAA;QACA,YAAA;QACA,iCAAA;QACA,0BAAA;QACA,8BAAA;CACA;AAEA;QACA,YAAA;CACA;CAEA","file":"Wallet.vue","sourcesContent":["<template>\n\n    <div class=\"dashboardWallet\" ref=\"dashboardWallet\">\n\n        <icon v-show=\"!this.opened && isSendingMoney\" class=\"miningStatus sendingImg jump\" icon='chevron-double-up' :style=\"\"></icon>\n\n        <icon v-show=\"!this.opened && isReceivingMoney\" :style=\"{\n            right: isSendingMoney ? '20px' : '4px',\n            marginBottom: isSendingMoney ? '-2px' : '0'}\" class=\"miningStatus receivingImg jump\"icon='chevron-double-down'>\n        </icon>\n\n        <div id=\"walletButton\" ref=\"walletMenuButton\" @click=\"this.toggleWallet\" :style=\"{\n            marginBottom: this.opened ? this.walletButtonMarginOpened+'px': this.walletButtonMarginClosed+'px',\n            top: this.opened ? this.buttonTopDistanceOpen : this.buttonTopDistanceClose,\n            borderTopLeftRadius: this.opened ? this.walletButtonRadiusLeftOpen+'px' : this.walletButtonRadiusLeftClose+'px',\n            borderTopRightRadius: this.opened ? this.walletButtonRadiusRightOpen+'px' : this.walletButtonRadiusRightClose+'px'}\">\n\n            <span id=\"walletButtonText\">\n                <div style=\"display: inline-block\">\n                    <icon class=\"buttonIcon statusWalletIcon\" :icon=\"this.opened ? 'chevron-down' : 'chevron-up'\" style=\"fill: black\"></icon>\n                    Wallet\n                </div>\n                <show-sum-balances ref=\"refShowSumBalances\" :style=\"{display: this.isMobile==false ? 'none' : 'inline-block'}\" :addresses=\"this.addresses\" :currency=\"this.currency\"> </show-sum-balances>\n            </span>\n        </div>\n\n        <div id=\"walletMenu\" ref=\"walletMenu\" :style=\"{\n            marginBottom: this.opened ? this.walletMarginOpened+'px': this.walletMarginClosed+'px',\n            top: this.opened ? this.buttonTopDistanceOpen : this.buttonTopDistanceClose,\n            marginTop: this.opened ? this.walletMenuMarginTopOpen : this.walletMenuMarginTopClose,\n            height: this.opened ? this.walletMenuHeightOpen : this.walletMenuHeightClosed}\">\n\n            <div id=\"dashboardWallet\">\n\n                <div class=\"walletController\">\n\n                    <div class=\"btn buttonTextStyle\" @click=\"this.handleAddNewAddress\" title=\"Add new wallet\">\n                        Add\n                    </div>\n\n                    <label class=\"myLabel\" title=\"Import existing wallet\">\n\n                        <input ref=\"importedAddress\" type=\"file\" v-on:change=\"this.handleImportAddress\" multiple size=\"50\" />\n\n                        <div class=\"btn buttonTextStyle\">\n                            Import\n                        </div>\n\n                    </label>\n\n                    <div class=\"btn buttonTextStyle\" title=\"Create Offline transaction\" v-on:click.stop=\"showOfflineTransactions\">\n                        Offline\n                    </div>\n\n                </div>\n\n                <div class=\"walletSection walletsContainer\" :style=\"{height: this.walletContentHeight+'px'}\">\n\n                    <div id=\"allWallets\">\n\n                        <Address v-for=\"(walletAddress, index) in this.addresses\"\n                             :isMiningAddress=\"miningAddress === walletAddress.address\"\n                             :key=\"walletAddress.address\"\n                             :id=\"'address'+walletAddress.address\"\n                             :ref=\"'address'+index\"\n                             :address=\"walletAddress.address\"\n                             style=\"padding-right: 20px\"\n                             @onPendingTransactionsChanges=\"handlePendingTransactionsChanges\"\n                        >\n\n                        </Address>\n\n                    </div>\n\n                </div>\n            </div>\n        </div>\n\n        <offline-transactions-modal ref=\"refOfflineTransactionsModal\" :address=\"this.miningAddress\"  />\n        <qr-scan-modal ref=\"qrScanModal\" />\n\n    </div>\n\n</template>\n\n\n\n<script>\n    var Vue = require('vue/dist/vue.min.js');\n\n    import icon from \"components/UI/icons/icon.vue\"\n    import Address from \"./Address/Address.vue\"\n    import BrowserHelpers from \"helpers/Browser.helpers\"\n    import ShowSumBalances from \"./Address/Balance/Balances/Show-Sum-Balances.vue\"\n    import Notification from \"helpers/Notification.helpers\"\n    import offlineTransactionsModal from \"./Address/Modals/Main-Modal/Address-main.modal.offline.vue\"\n\n    export default{\n\n        components:{\n            icon,\n            \"Address\": Address,\n            ShowSumBalances,\n            offlineTransactionsModal,\n        },\n\n        props: ['addresses', 'currency'],\n\n        data:  () => {\n            return {\n                opened: false,\n                balanceHover: false,\n                miningAddress: '',\n\n                isMobile:false,\n\n                sendingMoney:{},\n                receivingMoney:{},\n\n                walletButtonMarginOpened: 0,\n                walletButtonMarginClosed: 0,\n                buttonTopDistanceOpen: 0,\n                buttonTopDistanceClose: 0,\n                walletMarginOpened: 0,\n                walletMarginClosed: 0,\n                walletMenuMarginTopOpen: 0,\n                walletMenuMarginTopClose: 0,\n                walletMenuHeightOpen: 0,\n                walletMenuHeightClosed: 0,\n                walletContentHeight: 315,\n                walletButtonRadiusLeftOpen: 0,\n                walletButtonRadiusLeftClose: 0,\n                walletButtonRadiusRightOpen: 0,\n                walletButtonRadiusRightClose: 0,\n            }\n        },\n\n        mounted(){\n\n            //in browser\n            if (typeof window === \"undefined\") return false;\n\n            this.changeScreenBehavior();\n\n            WebDollar.StatusEvents.on(\"blockchain/mining/address\", (data)=>{\n                this.miningAddress = data.address;\n            });\n            this.miningAddress = WebDollar.Blockchain.Mining.minerAddress;\n\n            WebDollar.StatusEvents.on(\"wallet/transfer\", (data)=>{\n                console.warn(data)\n                let walletIndex = this.getMiningWalletIndex();\n                this.$refs['address'+walletIndex][0].openPrefilledTransferModal(data.toAddress, data.toAmount, data.toFee);\n            });\n\n            //onLoad    \n            BrowserHelpers.addEvent(window, \"load\", (event) => {\n                this.changeScreenBehavior();\n                this.walletResizeFix();\n            });\n\n            //onResize\n            BrowserHelpers.addEvent(window, \"resize\", (event) => {\n                this.changeScreenBehavior();\n                this.walletResizeFix();\n            });\n\n        },\n\n        computed:{\n\n            isSendingMoney(){\n                return Object.keys(this.sendingMoney).length !== 0;\n            },\n\n            isReceivingMoney(){\n                return Object.keys(this.receivingMoney).length !== 0;\n            }\n\n        },\n\n        methods: {\n\n            scanQrCode() {\n                this.$refs['qrScanModal'].showModal()\n            },\n\n            getMiningWalletIndex() {\n                if (!this.addresses || this.addresses.length == 0) {\n                    throw new Error('No addresses loaded, so unable to select mining wallet.');\n                }\n                let miningAddressIndex = this.addresses.map((a)=>a.address).indexOf(this.miningAddress);\n                if (miningAddressIndex == -1) {\n                    miningAddressIndex = 0; // fallback to first wallet index\n                }\n                return miningAddressIndex\n            },\n\n            showOfflineTransactions(){\n\n                this.$refs['refOfflineTransactionsModal'].showModal();\n\n            },\n\n            changeScreenBehavior(){\n\n                if (this.$refs['walletMenuButton'] === undefined) {\n                    console.log(\"not ready..\");\n                    return;\n                }\n\n                if (window.screenWidth < 831){\n\n                    this.isMobile = true;\n\n                    this.walletButtonMarginOpened = 452;\n                    this.walletButtonMarginClosed = 43;\n\n                    this.walletMarginOpened = 42;\n                    this.walletMarginClosed = -325;\n\n                    this.buttonTopDistanceOpen = '0';\n                    this.buttonTopDistanceClose = 'auto';\n\n                    this.walletMenuMarginTopOpen=this.$refs['walletMenuButton'].clientHeight;\n                    this.walletMenuMarginTopClose='0';\n\n                    this.walletMenuHeightOpen='100%';\n                    this.walletMenuHeightClosed='358px';\n\n                    this.walletContentHeight= window.outerHeight-250;\n\n                    this.walletButtonRadiusLeftOpen= 0;\n                    this.walletButtonRadiusLeftClose= 0;\n\n                    this.walletButtonRadiusRightOpen= 0;\n                    this.walletButtonRadiusRightClose= 0;\n\n                }else{\n\n                    this.isMobile=false;\n\n                    this.walletContentHeight= 315;\n\n                    this.walletButtonMarginOpened = 392;\n                    this.walletButtonMarginClosed = 30;\n\n                    this.walletMarginOpened = 34;\n                    this.walletMarginClosed = -325;\n\n                    this.buttonTopDistanceOpen = 'auto';\n                    this.buttonTopDistanceClose = 'auto';\n\n                    this.walletMenuMarginTopOpen=this.$refs['walletMenuButton'].clientHeight;\n                    this.walletMenuMarginTopClose='0';\n\n                    this.walletMenuHeightOpen='358px';\n                    this.walletMenuHeightClosed='0';\n\n                    this.walletButtonRadiusLeftOpen= 60;\n                    this.walletButtonRadiusLeftClose= 60;\n\n                    this.walletButtonRadiusRightOpen= 0;\n                    this.walletButtonRadiusRightClose= 0;\n\n                }\n\n            },\n\n            toggleWallet(){\n\n                this.opened = !this.opened;\n\n                if(window.screenWidth < 831){\n                    if (this.opened===true)\n                        document.getElementById('dashboardMining').setAttribute('style', 'display:none');\n                    else\n                        document.getElementById('dashboardMining').setAttribute('style', 'display:block');\n\n                }else\n                    document.getElementById('dashboardMining').setAttribute('style', 'display:block');\n\n\n            },\n\n            walletResizeFix(){\n\n                if(window.screenWidth < 831)\n                    if (this.opened===true)\n                        document.getElementById('dashboardMining').setAttribute('style', 'display:none');\n                    else\n                        document.getElementById('dashboardMining').setAttribute('style', 'display:block');\n\n                else\n                    document.getElementById('dashboardMining').setAttribute('style', 'display:block');\n\n\n            },\n\n            async handleAddNewAddress(){\n\n                // if (WebDollar.Blockchain.Wallet.addresses.length <= 2) {\n\n                    await WebDollar.Blockchain.Wallet.createNewAddress();\n                    Notification.addAlert(undefined, \"success\", \"Wallet Created\", WebDollar.Blockchain.Wallet.addresses[WebDollar.Blockchain.Wallet.addresses.length-1].address + \" has been added to your wallet!\", 5000);\n\n                // } else {\n                //\n                //     Notification.addAlert(undefined, \"warn\", \"Wallet Warning\", \"You can't add new address if you already have 3 addresses\", 5000);\n                //\n                // }\n\n            },\n\n            async handleImportAddress(){\n\n                // dropzone tutorial https://www.html5rocks.com/en/tutorials/file/dndfiles/\n\n                // Check for the various File API support.\n                if ((window.File && window.FileReader && window.FileList && window.Blob) === false){\n                    Notification.addAlert(undefined, \"error\", \"Import Error\", \"The File import is not fully supported in this browser\", 5000);\n                }\n\n                let fileInput = this.$refs['importedAddress'];\n\n                if ('files' in fileInput) {\n                    if (fileInput.files.length === 0) {\n                        Notification.addAlert(undefined, \"error\", \"Import Error\", \"No file selected\", 5000);\n                    } else {\n\n                        for (let i = 0; i < fileInput.files.length; i++) {\n\n                            let file = fileInput.files[i];\n                            let extension = file.name.split('.').pop();\n\n//                            console.log(file);\n//                            console.log(extension);\n\n                            if (extension === \"webd\") {\n                                let reader = new FileReader();\n\n                                try {\n                                    reader.onload = async (e) => {\n\n                                        //console.log(reader.result);\n                                        let data = JSON.parse(reader.result);\n\n                                        let answer = await WebDollar.Blockchain.Wallet.importAddressFromJSON(data);\n\n                                        if (answer.result === true){\n                                            Notification.addAlert(undefined, \"success\", \"Import Success\", answer.address + \" has been imported!\", 5000);\n                                        } else {\n                                            Notification.addAlert(undefined, \"error\", \"Import Error\", answer.message, 5000);\n                                        }\n\n                                    };\n\n                                } catch (exception){\n                                    Notification.addAlert(undefined, \"error\", \"Import Error\",\"Your Uploaded file is not a valid JSON format\", 5000);\n                                }\n\n                                reader.readAsText(file);\n                            } else {\n                                Notification.addAlert(undefined, \"error\",\"Import Error\", \"File not supported!\", 5000);\n                            }\n\n                        }\n\n\n                    }\n                }\n\n\n\n            },\n\n\n            handlePendingTransactionsChanges(){\n\n                this.receivingMoney = {};\n                this.sendingMoney = {};\n\n                let i=-1;\n                while (1===1){\n\n                    i++;\n                    let element = this.$refs['address'+i];\n                    if (element === undefined) break;\n\n                    element = element[0];\n\n                    for (l